import React from 'react';
import '../style/main.scss';
import Header from '../components/Header';
import Footer from '../components/Footer';
import Input from '../common/Input';
import Fetch from '../common/Fetch';
import Subscription from '../components/Subscription';
// import PropTypes from 'prop-types';
import SEO from "../components/seo"

        
class Contact extends React.Component{
    
    state = {
        name: '',
        email: '',
        phone: '',
        select: '',
        message: '',
        formErrors:{
            nameError: null,
            emailError: null,
            phoneError: null,
            selectError: null,
            messageError: null
        },
        successMessage: { type: 0, msg: '' },
        startFetch: null,
        countries: {},
        code: "+91"
    }
    
    componentDidMount() {
        Fetch('countrycode').then(data => this.setState({ countries: data })).catch(e => console.log(e))
    }

    sendForm = () => {
        const otherParams ={
            "name": this.state.name,
            "email": this.state.email,
            "phone": this.state.code+this.state.phone,
            "issueType": this.state.select,
            "issueDescription": this.state.message
        }

        Fetch("contactus", { ...otherParams }).then(data => {
            if(data.status){
                this.setState({
                    successMessage: { type: 1, msg: data.message }, name: '',
                    email: '', phone: '', message: '', select: ''
                })
                setTimeout(() => {
                    this.setState({
                        successMessage: { type: 1, msg: "" }
                    })
                }, 2000)
                console.log(data);
            }
            
        }).catch(e => this.setState({ successMessage: { type: 3, msg: "Please try again later" } }, console.log(e)))
    }

    handleChange=e=>{
        const {name, value} = e.target;

        const valid = e.isValid

        this.setState({  [name]: value })

        let formErrors = this.state.formErrors
        
        const selectedValue = document.getElementById("select");
             
        switch(name){
            case 'name': 
                formErrors.nameError = !valid
                break;

            case 'email':
                formErrors.emailError = !valid 
                break;

            case 'message':
                formErrors.messageError = !valid
                break;

            case 'phone':
                formErrors.phoneError = !valid

            case 'select':
                formErrors.selectError = selectedValue.selectedIndex !== 0 ? 0 : 1;
                break;
            }
        this.setState({formErrors}, console.log("formErrors", this.state.formErrors));


    }
    
    handleSubmit = e =>{
        e.preventDefault(); 
        const formErrors = this.state.formErrors;
    
        if(formErrors && formErrors.selectError){
               this.setState({successMessage: { type: 3, msg: "Fill all the fields" } })
            } else {
                this.sendForm()
                this.setState({name: '', email: '', phone: '', message: '', select: '' })
            }      
    }

render (){
    const { 
        name,
        email,
        phone,
        message,
        select,
        successMessage,
        formErrors,
        countries
    } = this.state; 
    
    return (
        <main>
            <SEO title="Contact Us" />
            <div className="bg-theme">
                <div className="header-secondary"><Header/></div>
                <div className="head-banner">
                    <div className="container">
                        <div className="row">
                            <div className="col-xl-6 col-lg-6 col-md-12 float-none m-auto text-center">
                                <h1 className="color-white">Let’s connect</h1>
                                <p className="color-white f-18">We’d love to assist you in starting your educational journey with us. </p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div className="bg-light">
                <div className="section-spacing ">
                    <div className="container">
                        <div className="row contact-details">
                            <div className="col-xl-4 col-lg-4 col-md-12">
                                <div className="row">
                                    <div className="col-xl-10 col-lg-12 col-xs-12">
                                        <div className="">
                                            <h3 className="f-20">Office Address</h3>
                                            <p>Microtek Building Plot No. A 12, 3rd Floor, Rajiv Gandhi Technology park,
                                            160101, Chandigarh.
                                        </p>
                                        </div>
                                        <div className=" border-top mT30 pT30">
                                            <h3 className="f-20">Contact our Support</h3>
                                            <p><strong>Phone: </strong> +91 9779421738</p>
                                            <p><strong>Fax: </strong> (+096) 204 353 6684</p>
                                            <p><strong>Email: </strong> saasland@gmail.com</p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="col-xl-8 col-lg-8 col-md-12 md-mt-30">
                                <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3429.78368049446!2d76.84342691508593!3d30.72448118164001!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x390f9360b1e76d31%3A0x423d8f6c94c1261a!2sPrepladder!5e0!3m2!1sen!2sin!4v1585557883327!5m2!1sen!2sin" width="100%" height="450" ></iframe>
                            </div>
                        </div>
                        <div className="row mT60">
                            <div className="col-xl-12 col-lg-12">
                                <h3 className="f-24">Leave a Message</h3>
                                <form onSubmit={this.handleSubmit} noValidate className="form">
                                    <div className="form-group">
                                        <div className="row">

                                            <div className="col-xl-6 col-lg-6 col-md-6 col-sm-12">
                                                <Input
                                                    type="text"
                                                    value={name}
                                                    id="name"
                                                    validFor="text"
                                                    onChange={this.handleChange}
                                                    placeholder="Your Name"
                                                    className="form-control"
                                                    name="name"
                                                />
                                            </div>
                                            <div className="col-xl-6 col-lg-6 col-md-6 col-sm-12 sm-mt-25">
                                                <Input
                                                    type="email"
                                                    value={email}
                                                    id="email"
                                                    validFor="email"
                                                    onChange={this.handleChange}
                                                    placeholder="Enter Email"
                                                    className="form-control"
                                                    name="email"
                                                />                                           
                                            </div>
                                        </div>
                                    </div>
                                    <div className="form-group">
                                        <div className="row">
                                            <div className="col-xl-6 col-lg-6 col-md-6 col-sm-12">
                                            <div className="form-control contact-phone">
                                                    <select
                                                        name="code"
                                                        onChange={this.handleChange}>
                                                        <option key="0" value="+91">+91</option>
                                                        {
                                                        countries.response && countries.response.length ?
                                                            countries.response.map((code, i) => (
                                                                 <option key={i + 1} value={code.dial_code}>{code.dial_code}</option>
                                                            )) : null
                                                        }
                                                    </select>
                                                     <Input
                                                    type="tel"
                                                    id="phone"
                                                    value={phone}
                                                    validFor="mobileNumber"
                                                    className=""
                                                    validClassName="valid"
                                                    invalidClassName="invalid"
                                                     placeholder="Phone number" 
                                                    name="phone"
                                                    onChange={this.handleChange}
                                                />
                                             </div>
                                            </div>

                                            <div className="col-xl-6 col-lg-6 col-md-6 col-sm-12 sm-mt-25">
                                                <select name="select"
                                                        onChange={this.handleChange}
                                                        id="select"
                                                        className={formErrors.selectError === 0 ? "form-control valid" : formErrors.selectError === 1 ? "form-control invalid" : "form-control"  }
                                                        >
                                                    <option value="0">Subject</option>
                                                    <option value="1">Subject 1</option>
                                                    <option value="2">Subject 2</option>
                                                    <option value="3">Subject 3</option>
                                                    <option value="4">Subject 4</option>
                                                </select>
                                            </div>

                                        </div>
                                    </div>
                                    <div className="form-group">
                                        <Input
                                            validFor="textarea"
                                            type="text"
                                            id="text"
                                            value={message}
                                            className={"form-control" }
                                            placeholder="Write your message..." 
                                            style={{maxWidth:"100%",minWidth:'100%'}}
                                            name="message"
                                            onChange={this.handleChange}
                                        />
                                    </div>
                                    {
                                        successMessage.type === 1
                                            ?
                                            <div style={{ color: "green", fontSize: "14px" }}> {successMessage.msg} </div>
                                            :
                                            <div style={{ color: "red", fontSize: "12px" }}> {successMessage.msg} </div>
                                    }

                                    <div className="form-group">
                                        <input type="submit" className="btn btn-theme" 
                                        value="Send Message" name="submit btn"
                                         />
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <Subscription countryCode={this.state.countries.response}/>
            <Footer/>
        </main>
    );
}
    
};

export default Contact;